<template>
  <v-container fill-height>
    <v-row
      class="justify-center align-center"
    >
      <v-col
        xs="12"
        sm="6"
        md="4"
        lg="4"
        xl="4"
      >
        <v-card elevation="12">
          <v-toolbar
            dense
            color="primary"
            dark
          >
            <v-toolbar-title>RCCL</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-progress-circular
              v-show="isLoading"
              indeterminate
              color="white"
              width="2"
            ></v-progress-circular>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field
                v-model="email"
                prepend-icon="mdi-email"
                name="email"
                label="Email"
                type="email"
              ></v-text-field>
              <v-text-field
                v-model="password"
                prepend-icon="mdi-lock"
                name="password"
                label="Senha"
                type="password"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              large
              @click="submit"
            >Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import firebase from "@/firebaseConfig";
export default {
  data: () => ({
    email: '',
    isLoading: false,
    password: ''
  }),
  methods: {
    submit () {
      this.isLoading = true
      firebase.auth().signInWithEmailAndPassword(this.email, this.password)
        .then(() => {
          this.isLoading = false
          this.$router.push(this.$route.query.redirect || '/dashboard')
        })
        .catch((error) => {
          // var errorCode = error.code;
          var errorMessage = error.message;
          console.error(errorMessage);
          this.isLoading = false
        });
    }
  }
}
</script>